<template>
<div class="cart">
    <div class="cartTitle">
        <div class="left" @click="goBack">
            <i class="icon-back"></i>
        </div>
        <div class="center">购物车</div>
        <div class="right" @click="clearAll()">清空</div>
    </div>
    <div class="darkLine"></div>
    <div class="cartContent" ref="fav">
        <ul class="cartBox" v-if="goodsList.length > 0">
            <li v-for="(goodsIn,index) in goodsList">
                <span class="icon-tick" :class="{'check': goodsIn.checked}" @click="selectedProduct(goodsIn)"></span>
                <div class="cartLeft">
                    <img :src="goodsIn.img" width="79" height="77"/>
                </div>
                <div class="cartCenter">
                    <span class="cartName">{{goodsIn.name}}</span><span class="cartPrice">￥{{goodsIn.price * goodsIn.count}}</span>
                    <p class="cartColor">款式：{{goodsIn.color}}，单价：{{goodsIn.price}}</p>
                    <div class="count">
                        <cartControl :goodsIn="goodsIn"></cartControl>
                    </div>
                </div>
                <div class="cartRight" @click="del(goodsIn.id)">
                    <i class="icon-close"></i>
                </div>
            </li>
        </ul>
        <div class="content-null" v-else>
            <i class="icon-cart"></i>
            <p>客官，您的购物车还空着</p>
        </div>
    </div>
    <div class="line border-1px"></div>
    <div class="cartFooter">
        <div class="left">
            合计￥{{sum}}元
        </div>
        <div class="right" @click="toPay">
            结算
        </div>
    </div>
</div>
</template>

<script type="text/ecmascript-6">
import BScroll from 'better-scroll';
import cartControl from '../cartControl/cartControl';
import { mapMutations, mapGetters } from 'vuex';
export default {
    data() {
        return {
        };
    },
    computed: {
        ...mapGetters(
            ['sum']
        ),
        goodsList() {
            return this.$store.state.goodsList;
        }
    },
    watch: {
        'this.$store.state.goodsList'() {
            this.$nextTick(() => {
                this._initScroll();
            });
        }
    },
    mounted() {
        this.$nextTick(() => {
            this._initScroll();
        });
    },
    methods: {
        ...mapMutations(
            ['deleteGoods', 'updateGoods']
        ),
        findPosition(id) {
            return this.goodsList.findIndex(goodsIn => {
                return goodsIn.id === id;
            });
        },
        del(id) {
            var i = this.findPosition(id);
            this.$store.commit('deleteGoods', i);
        },
        clearAll() {
            this.$store.commit('deleteAll');
        },
        _initScroll() {
            if (!this.scroll) {
                this.scroll = new BScroll(this.$refs.fav, {
                    click: true
                });
            }
        },
        selectedProduct(goodsIn) {
            console.log('1');
            if (typeof goodsIn.checked === 'undefined') {
                this.$set(goodsIn, 'checked', true);
            } else {
                goodsIn.checked = !goodsIn.checked;
            }
        },
        goBack() {
            this.$router.back();
        },
        toPay() {
            this.$confirm('请确认是否支付此产品，金额为' + '￥' + this.sum + '元', '支付框', {
                confirmButtonText: '确认支付',
                cancelButtonText: '取消',
                type: 'success',
                center: true,
                showClose: false,
                closeOnClickModal: false
            }).then(() => {
                this.$message({
                    type: 'success',
                    message: '支付成功!'
                });
                this.clearAll();
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消'
                });
            });
        }
    },
    components: {
        cartControl
    }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "../../common/stylus/mixin";
.cart
    width:100%
    .cartTitle
        width:100%
        position:fixed
        top:0
        left:0
        height:50px
        z-index:200
        background:#fff
        display:flex
        .left
            flex:0 0 20px
            width:20px
            line-height:50px
            margin-left:15px
            cursor:pointer
        .center
            flex:1
            line-height:50px
            text-align:center
            font-size:14px
        .right
            flex:0 0 30px
            width:30px
            line-height:50px
            font-size:12px
            margin-right:15px
            cursor:pointer
    .darkLine
        width:100%
        height:5px
        background:#EEEEEE
        margin-top:50px
    .cartContent
        width:100%
        position:absolute
        top:55px
        left:0
        bottom:50px
        overflow:hidden
        .cartBox
            width:100%
            & > li
                height:116px
                display:flex
                border-bottom:1px solid #EEEEEE
                .icon-tick
                    flex:0 0 15px
                    margin-top:45px
                    border:1px solid #e9e9e9
                    color:#e9e9e9
                    width:15px
                    height:15px
                    border-radius:50%
                    margin-left:15px
                .check
                    color:#62CED7
                    border:1px solid #62CED7
                .cartLeft
                    flex:0 0 100px
                    width:100px
                    text-align:center
                    & > img
                        margin-top:10px
                .cartCenter
                    flex:1
                    margin-top:15px
                    .cartName
                        font-size:14px
                        font-weight:600
                        vertical-align:top
                    .cartColor
                        font-size:12px
                        font-weight:600
                        color:#999999
                        margin-top:18px
                    .cartPrice
                        font-size:14px
                        font-weight:600
                        float:right
                .cartRight
                    flex:0 0 20px
                    width:20px
                    position:relative
                    .icon-close
                        position:absolute
                        bottom:20px
                        right:15px
                        cursor:pointer
        .content-null
            width:100%
            text-align:center
            margin-top:80px
            .icon-cart
                font-size:40px
                color:#e9e9e9
            & > p
                color:#666666
    .cartFooter
        width:100%
        height:50px
        position:fixed
        bottom:0
        left:0
        z-index:200
        background:#fff
        display:flex
        font-size:14px
        .left
            flex:1
            line-height:50px
            padding-left:40px
        .right
            flex:0 0 124px
            width:124px
            background:#62CED7
            height:50px
            line-height:50px
            text-align:center
            cursor:pointer
</style>


