<template>
    <div class="topic" ref="topicBox">
        <div class="topic-content">
            <div class="topicT" v-for="item in topic">
                <img :src="item.bannerImg"  width="100%" height="200"/>
                <div class="imgBox" ref="imgBox">
                    <ul ref="sImg">
                        <li v-for="(item,index) in item.book">
                            <img :src="item.img" width="100" height="100">
                            <div class="detail">
                                <p class="name">{{item.name}}</p>
                                <p class="price">￥{{item.price}}</p>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
import BScroll from 'better-scroll';
export default {
    data() {
        return {
            topic: [
                {
                    bannerImg: 'http://f.cloudadmx.com/group2/M00/00/1F/wKgABVoCdXWAU6uZAALCjTFI54Q144.png',
                    book: [
                        {
                            img: 'http://f.cloudadmx.com/group1/M00/00/22/wKgABFoCdaGAPeWnAAB7Ojr5tTQ792.png',
                            name: '纯色牛皮本',
                            price: 26
                        },
                        {
                            img: 'http://f.cloudadmx.com/group1/M00/00/21/wKgAA1oCdamAJG01AABu9bN9Xgc917.png',
                            name: '棉麻质感笔记',
                            price: 34
                        },
                        {
                            img: 'http://f.cloudadmx.com/group1/M00/00/22/wKgABFoCdbWAMCB6AAEM5oX6ZYQ490.png',
                            name: '清新文艺风',
                            price: 18
                        },
                        {
                            img: 'http://f.cloudadmx.com/group1/M00/00/21/wKgAA1oCdbuAebGrAABFxEuWDcg673.png',
                            name: '蓝色棉麻本',
                            price: 23
                        }
                    ]
                },
                {
                    bannerImg: 'http://f.cloudadmx.com/group2/M00/00/20/wKgABloCdX6AYwtpAAV7fUKCYdE366.png',
                    book: [
                        {
                            img: 'http://f.cloudadmx.com/group2/M00/00/1F/wKgABVoCdYiAFhQdAADMvuq-_Vg973.png',
                            name: '森林主题贴纸',
                            price: 18
                        },
                        {
                            img: 'http://f.cloudadmx.com/group2/M00/00/20/wKgABloCdY6AIwtcAAEcOhjwfAo831.png',
                            name: '粉色纹理笔袋',
                            price: 18
                        },
                        {
                            img: 'http://f.cloudadmx.com/group2/M00/00/1F/wKgABVoCdZSAXKCmAAEjAiAp3EY924.png',
                            name: '木纹纸胶带',
                            price: 26
                        },
                        {
                            img: 'http://f.cloudadmx.com/group1/M00/00/21/wKgAA1oCdZmAHKRgAADH-ER4kKQ093.png',
                            name: '猫头鹰书架',
                            price: 34
                        }
                    ]
                }
            ]
        };
    },
    watch: {
        'topic'() {
            this.$nextTick(() => {
                this._initScroll();
                this._initPics();
            });
        }
    },
    mounted() {
        this.$nextTick(() => {
            this._initScroll();
            this._initPics();
        });
    },
    methods: {
        _initScroll() {
           if (!this.scroll) {
               this.scroll = new BScroll(this.$refs.topicBox, {
                  click: true
               });
           }
        },
        _initPics() {
            for (var i = 0; i < this.topic.length; i++) {
                if (this.topic[i].book) {
                    let picWidth = 100;
                    let margin = 8;
                    let width = (picWidth + margin) * this.topic[i].book.length - margin;
                    this.$refs.sImg[i].style.width = width + 'px';
                    this.$nextTick(() => {
                        for (var i = 0; i < this.topic.length; i++) {
                            var nameA = this.oScroll + i;
                            if (!nameA) {
                                nameA = new BScroll(this.$refs.imgBox[i], {
                                    scrollX: true,
                                    scrollY: false,
                                    eventPassthrough: 'vertical'
                                });
                            } else {
                                nameA.refresh();
                            }
                        }
                    });
                }
            }
        }
    }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
.topic
    width:100%
    position:absolute
    top:50px
    bottom:50px
    left:0
    overflow:hidden
    .topic-content
        width:100%
        .topicT
            width:100%
            margin-bottom:18px
            .imgBox
                width:100%
                white-space: nowrap
                margin-top:5px
                height:155px
                overflow:hidden
                & > ul
                    padding-left:10px
                    & > li
                        display: inline-block
                        margin-right: 6px
                        width: 100px
                        height:155px
                        overflow:hidden
                        &:last-child
                            margin: 0
                        & > img
                            border-radius:8px
                        .detail
                            text-align:center
                            font-size:0
                            .name
                                font-size:12px
                                color:#666666
                            .price
                                font-size:14px
                                color:#62CED7
                                margin-top:0px
                                font-weight:600

</style>


