<template>
<div>
    <div v-show="showFlag" class="goods">
        <div class="inHeader">
            <div class="left" @click="goodShow">
                <i class="icon-back"></i>
            </div>
            <div class="right">
                <i class="icon-skip"></i>
            </div>
        </div>
        <div class="goodsWrapper">
            <div class="content">
                <img :src="goodsIn.img"/>
                <div class="line border-1px"></div>
                <div class="cost">
                    <div class="left">
                        <div class="top">
                            {{goodsIn.name}}-{{goodsIn.id}}
                        </div>
                        <div class="bottom">￥{{goodsIn.price}}</div>
                    </div>
                    <div class="right">
                        <div class="icon-favorites remove" :class="{'add': goodsIn.isActive}"@click="addPro($event)"></div>
                        <div class="bottom">{{goodsIn.add}}</div>
                    </div>
                </div>
                <div class="darkLine"></div>
                <div class="recommend">
                    <ul>
                        <li v-for="item in goodsIn.recommend">
                            <img :src="item.img" width="48px" height="71"/>
                            <p>{{item.color}}</p>
                        </li>
                    </ul>
                </div>
                <div class="darkLine"></div>
            </div>
            <div class="cartFooter">
                <div class="line border-1px"></div>
                <div class="footer">
                    <div class="left">
                        <i class="icon-atm"></i>
                    </div>
                    <div class="right">
                        <div class="rLeft" @click="closeAddShop">加入购物车</div>
                        <div class="rRight" @click="showCart">立即购买</div>
                    </div>
                </div>
            </div>
            <div v-show="addShop" class="dark" @click="closeAddShop"></div>
            <div class="addShop" v-show="addShop">
                <div class="close" @click="closeAddShop">
                    <i class="icon-close"></i>
                </div>
                <div class="cartContent">
                    <div class="content">
                        <div class="top">
                            <img :src="goodsIn.img" width="79" height="77"/>
                            <p class="topName">{{goodsIn.name}}</p>
                            <p class="topPrice">￥{{goodsIn.price}}</p>
                        </div>
                        <div class="bottom">
                            <p class="bottomTitle">颜色</p>
                            <div class="bottomItem">
                                <ul>
                                    <li :class="{'check': index == currentIndex}" v-for="(item,index) in goodsIn.recommend" @click="currentIndex = index">{{item.color}}</li>
                                </ul>
                            </div>
                            <p class="bottomCount">数量</p>
                            <div class="count">
                                <cartControl :goodsIn="goodsIn"></cartControl>
                            </div>
                            <div class="confirm">
                                <button @click="addShopCart(id)">加入购物车</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div v-show="cart" class="dark" @click="darkHide"></div>
            <div v-show="cart" class="cart">
                <div class="close" @click="closeCart">
                    <i class="icon-close"></i>
                </div>
                <div class="cartContent">
                    <div class="content">
                        <div class="top">
                            <img :src="goodsIn.img" width="79" height="77"/>
                            <p class="topName">{{goodsIn.name}}</p>
                            <p class="topPrice">￥{{goodsIn.price}}</p>
                        </div>
                        <div class="bottom">
                            <p class="bottomTitle">颜色</p>
                            <div class="bottomItem">
                                <ul>
                                    <li :class="{'check': index == currentIndex}" v-for="(item,index) in goodsIn.recommend" @click="currentIndex = index">{{item.color}}</li>
                                </ul>
                            </div>
                            <p class="bottomCount">数量</p>
                            <div class="count">
                                <cartControl :goodsIn="goodsIn"></cartControl>
                            </div>
                            <div class="confirm">
                                <button @click="totalMoney(goodsIn)">确定</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script type="text/ecmascript-6">
import cartControl from '../cartControl/cartControl';
import shopCart from '../shopCart/shopCart';
export default {
    data() {
        return {
            showFlag: false,
            cart: false,
            addCart: false,
            addShop: false,
            selectedShop: {},
            currentIndex: 1,
            totalPrice: 0,
            id: this.goodsIn.id,
            addSuccess: false
        };
    },
    props: {
        goodsIn: {
            type: Object
        }
    },
    components: {
        cartControl,
        shopCart
    },
    methods: {
        goodShow() {
            this.showFlag = false;
            console.log('1');
        },
        show() {
            this.showFlag = true;
        },
        closeAddShop() {
            if (this.addShop) {
                this.addShop = false;
            } else {
                this.addShop = !this.addShop;
            }
        },
        addPro(event) {
            if (this.goodsIn.isActive) {
                this.$message({
                    message: '取消收藏',
                    type: 'success',
                    customClass: 'tip'
                });
                this.goodsIn.isActive = false;
            } else {
                this.$message({
                    message: '成功收藏',
                    type: 'success',
                    customClass: 'tip'
                });
            this.goodsIn.isActive = true;
            }
        },
        showCart() {
            this.cart = true;
        },
        closeCart() {
            this.cart = false;
        },
        darkHide() {
            this.cart = false;
        },
        addShopCart(id) {
            var list = this.$store.state.goodsList;
            console.log(list);
            var idExist = list.find((item) => {
                return item.id === id;
            });
            if (!idExist) {
                    var data = {
                            id: this.goodsIn.id,
                            name: this.goodsIn.name,
                            price: this.goodsIn.price,
                            count: this.goodsIn.count,
                            color: this.goodsIn.recommend[this.currentIndex].color,
                            img: this.goodsIn.img
                    };
                    this.$store.commit('addGoods', data);
                    this.addSuccess = true;
                    this.$message({
                            message: '已成功加入购物车',
                            type: 'success',
                            customClass: 'tip'
                    });
            } else {
                    return '已加入购物车';
            }
            this.addShop = false;
        },
        totalMoney(goodsIn) {
            this.totalPrice = goodsIn.price * goodsIn.count;
            console.log(this.totalPrice);
            if (this.totalPrice > 0) {
                this.$confirm('请确认是否支付此产品，金额为' + '￥' + this.totalPrice + '元', '支付框', {
                        confirmButtonText: '确认支付',
                        cancelButtonText: '取消',
                        type: 'success',
                        center: true,
                        showClose: false,
                        closeOnClickModal: false
                    }).then(() => {
                        this.$message({
                            type: 'success',
                            message: '支付成功!'
                        });
                        this.cart = false;
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消'
                        });
                        this.cart = false;
                    });
                }
            }
        }
    };
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "../../common/stylus/mixin";
.goods
    position:fixed
    top:0px
    left:0
    bottom:0px
    z-index:50
    width:100%
    background:#fff
    .inHeader
       display:flex
       justify-content:space-between
       height:40px
       width:100%
       position:fixed
       top:0
       left:0
       .left
          margin-left:15px
          line-height:40px
          cursor:pointer
       .right
          margin-right:15px
          line-height:40px
          cursor:pointer
    .goodsWrapper
        position:absolute
        top:40px
        bottom:0
        left:0
        width:100%
        overflow:hidden
        .content
           width:100%
           text-align:center
           & > img
              padding:25px 0
           .line
              width:100%
              border-1px(rgba(7,17,27,0.1))
           .cost
              width:100%
              display:flex
              justify-content:space-between
              margin:10px 0
              .left
                 margin-left:15px
                 .top
                    font-size:15px
                    color:#333333
                 .bottom
                    font-size:15px
                    color:#62ced7
                    text-align:left
                    margin-top:5px
              .right
                 margin-right:15px
                 cursor:pointer
                 .remove
                    color:#333
                 .add
                    color:#62CED7
                 .bottom
                     font-size:11px
                     color:#62ced7
           .darkLine
              width:100%
              height:8px
              background:#EEEEEE
           .recommend
              width:100%
              & > ul
                 display:flex
                 justify-content:space-around
                 padding-top:20px
                 & > li
                    width:65px
                    height:112px
                    text-align:center
                    & > p
                       margin-top:5px
                       font-size:12px
                       color:#666666
        .cartFooter
           width:100%
           height:50px
           position:fixed
           bottom:0
           left:0
           background:#fff
           .line
               width:100%
               border-1px(rgba(7,17,27,0.1))
           .footer
               width:100%
               .left
                   flex:0 0 75px
                   width:75px
                   text-align:center
                   .icon-atm
                      font-size:20px
                      line-height:50px
               .right
                   flex:1
                   display:flex
                   flex-direction:row
                   text-align:center
                   font-size:15px
                   .rLeft
                       flex:1
                       background:#E5E5E6
                       line-height:50px
                       cursor:pointer
                   .rRight
                       flex:1
                       background:#62CED7
                       line-height:50px
                       cursor:pointer
        .dark
             position:fixed
             top:0px
             left:0
             bottom:0px
             z-index:50
             width:100%
             background:#666666
             opacity:0.6
        .cart,.addShop
           position:fixed
           bottom:0
           left:0
           width:100%
           height:400px
           background:#fff
           z-index:100
           .close
               width:100%
               height:15px
               cursor:pointer
               .icon-close
                   position:absolute
                   right:5px
                   top:5px
           .cartContent
                width:100%
                position:absolute
                top:15px
                bottom:0
                left:0
                overflow:hidden
                .top
                    text-align:center
                    .topName
                        font-weight:600
                    .topPrice
                        font-weight:600
                        color:#62CED7
                .bottom
                    text-align:left
                    padding:0 30px
                    .bottomCount
                        color:#333333
                        font-weight:600
                    .bottomTitle
                        color:#333333
                        font-weight:600
                    .bottomItem
                        width:100%
                        & > ul
                            width: 100%
                            height:25px
                            & > li
                                display:inline-block
                                margin-right:10px
                                color:#666666
                                border:1px #fff solid
                                border-radius:5px
                                padding:5px
                            .check
                                border:1px #62CED7 solid
                                background:#62CED7
                                color:#fff
                    .confirm
                        width:100%
                        text-align:center
                        height:25px
                        margin-top:35px
                        margin-bottom:50px
                        & > button
                            width:70%
                            line-height:25px
                            background:#62CED7
                            color:#fff
                            border:none
                            border-radius:5px
                            cursor:pointer
</style>


