<template>
<div class="people" ref="peopleBox">
    <div class="people-content" ref="people">
        <ul ref="peopleContent">
            <li v-for="item in people">
                <div class="pageTitle">
                    <div class="pLeft">
                        <img :src="item.head" width="40" height="40"/>
                    </div>
                    <div class="pCenter">
                        <div class="top">{{item.name}}</div>
                        <div class="bottom">
                            <span>文章 {{item.page}}</span>
                            <span>视频 {{item.video}}</span>
                            <span>图片 {{item.photo}}</span>
                        </div>
                    </div>
                    <div class="pRight">
                       <span :class="{ add: item.isActive }" @click="selectedPage(item)"> +关注</span>
                    </div>
                </div>
                <div class="pageContent" ref="imgBox">
                    <ul class="imgItem" ref="imgItem">
                        <li v-for="item in item.share">
                            <img :src="item.img" width="183" height="135"/>
                        </li>
                    </ul>
                </div>
            </li>
        </ul>
    </div>
</div>
</template>

<script type="text/ecmascript-6">
import BScroll from 'better-scroll';
    export default{
        data() {
            return {
                people: [
                    {
                        head: 'http://f.cloudadmx.com/group1/M00/00/24/wKgABFoFWt6ACO8LAAA2wCW4WlE426.png',
                        name: 'Mango-鱼',
                        page: 24,
                        video: 18,
                        photo: 237,
                        share: [
                            {
                                img: 'http://f.cloudadmx.com/group1/M00/00/24/wKgAA1oFWuqAei8TAAJwflCzKp8595.png'
                            },
                            {
                                img: 'http://f.cloudadmx.com/group1/M00/00/24/wKgABFoFWvOAID-FAAJ3yPDE7FI787.png'
                            }
                        ]
                    },
                    {
                        head: 'http://f.cloudadmx.com/group1/M00/00/24/wKgABFoFWsqAVGZYAAA3j8Wk7rM335.png',
                        name: '君君',
                        page: 46,
                        video: 20,
                        photo: 129,
                        share: [
                            {
                                img: 'http://f.cloudadmx.com/group1/M00/00/24/wKgAA1oFWv2AEhslAAHhrpIu--4028.png'
                            },
                            {
                                img: 'http://f.cloudadmx.com/group1/M00/00/24/wKgABFoFWwSAOocUAAH5jmcLVvI910.png'
                            }
                        ]
                    },
                    {
                        head: 'http://f.cloudadmx.com/group1/M00/00/24/wKgAA1oFWtKAQwjIAAA1L7wagG8683.png',
                        name: '考拉文子',
                        page: 34,
                        video: 23,
                        photo: 35,
                        share: [
                            {
                                img: 'http://f.cloudadmx.com/group1/M00/00/24/wKgAA1oFWuqAei8TAAJwflCzKp8595.png'
                            },
                            {
                                img: 'http://f.cloudadmx.com/group1/M00/00/24/wKgABFoFWwSAOocUAAH5jmcLVvI910.png'
                            }
                        ]
                    }
                ]
            };
        },
        watch: {
            'people'() {
                this.$nextTick(() => {
                    this._initScroll();
                    this._initPics();
                });
            }
        },
        mounted() {
            this.$nextTick(() => {
                this._initScroll();
                this._initPics();
            });
        },
        methods: {
            sight(event) {
                for (var i = 0; i < this.people.length; i++) {
                    console.log(i);
                }
            },
            _initScroll() {
                if (!this.scroll) {
                    this.scroll = new BScroll(this.$refs.peopleBox, {
                        click: true
                    });
                }
            },
            _initPics() {
                for (var i = 0; i < this.people.length; i++) {
                    if (this.people[i].share) {
                        let picWidth = 183;
                        let margin = 10;
                        let width = (picWidth + margin) * this.people[i].share.length - margin;
                        this.$refs.imgItem[i].style.width = width + 'px';
                        this.$nextTick(() => {
                            for (var i = 0; i < this.people.length; i++) {
                                var nameA = this.oScroll + i;
                                if (!nameA) {
                                    nameA = new BScroll(this.$refs.imgBox[i], {
                                        scrollX: true,
                                        eventPassthrough: 'vertical'
                                    });
                                } else {
                                    nameA.refresh();
                                }
                            }
                        });
                    }
                }
            },
            selectedPage(item) {
                if (typeof item.isActive === 'undefined') {
                    this.$set(item, 'isActive', true);
                    this.$message({
                        message: '关注成功',
                        type: 'success'
                    });
                } else {
                    item.isActive = !item.isActive;
                    this.$message({
                        message: '取消关注',
                        type: 'success'
                    });
                }
            }
        }
    };
</script>

<style lang="stylus" rel="stylesheet/stylus">
.people
    width:100%
    position:absolute
    top:50px
    bottom:50px
    left:0
    overflow:hidden
    .people-content
        width:100%
        overflow:hidden
        margin-top:10px
        &:last-child
            margin-bottom:30px
        & > ul
            width:100%
            overflow:hidden
            & > li
                margin-bottom:10px
                .pageTitle
                   display:flex
                   .pLeft
                      flex:0 0 40px
                      width:40px
                      margin-left:15px
                   .pCenter
                      flex:1
                      display:flex
                      flex-direction:column
                      padding:0 10px
                      .top
                         font-size:14px
                         color:#333333
                         font-weight:700
                         margin-bottom:5px
                      .bottom
                         font-size:12px
                         color:#999999
                         margin-top:5px
                         & > span
                            margin-right:5px
                            font-weight:600
                   .pRight
                      flex:0 0 48px
                      width:48px
                      height:40px
                      margin-right:15px
                      & > span
                         border:1px solid #62CED7
                         border-radius:5px
                         line-height:40px
                         padding:5px
                         font-size:10px
                   .add
                      color:#fff
                      background:#62CED7
                   .remove
                      color:#333
                      background:none
                .pageContent
                   width:100%
                   white-space: nowrap
                   margin-top:15px
                   overflow:hidden
                   & > ul
                      margin-left:15px
                      & > li
                         display:inline-block
                         width:183px
                         height:135px
                         margin-right:10px
                         &:last-child
                            margin-right:0
                         & > img
                            border-radius:5px
</style>


