<template>
<div class="me">
    <div class="meHeader">
        <div class="left">
            我
        </div>
        <div class="right">
           <router-link to="/">
               <i class="icon-back"></i>
           </router-link>
        </div>
    </div>
    <div class="line border-1px"></div>
    <div class="meBox" ref="meBox">
        <div class="meContent">
            <div class="meTitle">
                <div class="left">
                    <img :src="me.head" width="61" height="61"/>
                </div>
                <div class="center">
                    <div class="top">{{me.name}}</div>
                    <div class="bottom">{{me.summary}}</div>
                </div>
                <div class="right">&gt;</div>
            </div>
            <div class="lineDark"></div>
            <div class="order">
                <div class="orderTitle">
                    <div class="orderLeft">
                        我的订单
                    </div>
                    <div class="orderRight">></div>
                </div>
                <div class="line border-1px"></div>
                <div class="orderContent">
                    <ul>
                        <li v-for="item in me.order">
                            <img :src="item.img" width="25" height="25"/>
                            <p class="orderFont">{{item.name}}</p>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="lineDark"></div>
            <div class="list">
                <ul>
                    <li v-for="item in me.item">
                        <div class="listLeft">
                            <img :src="item.img" width="20" height="20"/>
                        </div>
                        <div class="listRight">
                            <div class="innerLeft">
                                {{item.name}}
                            </div>
                            <div class="innerRight">></div>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</div>
</template>

<script type="text/ecmascript-6">
import BScroll from 'better-scroll';
    export default {
        data() {
            return {
                me: {
                    head: 'http://f.cloudadmx.com/group1/M00/00/24/wKgABFoFWt6ACO8LAAA2wCW4WlE426.png',
                    name: '文艺笑笑',
                    summary: '那是未来人们凝视的眼睛',
                    item: [
                        {
                            name: '售后',
                            img: 'http://f.cloudadmx.com/group2/M00/00/23/wKgABVoGqreAQPSuAAAHGxchLfw392.png'
                        },
                        {
                            name: '客服',
                            img: 'http://f.cloudadmx.com/group2/M00/00/23/wKgABloGqqqABDLuAAAGg3Ax68I977.png'
                        },
                        {
                            name: '草稿',
                            img: 'http://f.cloudadmx.com/group2/M00/00/23/wKgABloGqsGAb6GOAAAFUMCnaWI127.png'
                        },
                        {
                            name: '收藏',
                            img: 'http://f.cloudadmx.com/group2/M00/00/23/wKgABVoGqpiATRyYAAAGkFJG52U277.png'
                        },
                        {
                            name: '礼券',
                            img: 'http://f.cloudadmx.com/group2/M00/00/23/wKgABVoGqsqAE7qEAAAFkK08Rhk728.png'
                        },
                        {
                            name: '地址',
                            img: 'http://f.cloudadmx.com/group2/M00/00/23/wKgABVoGrZiANEbfAAAG94ukEdY873.png'
                        }
                    ],
                    order: [
                        {
                            name: '待付款',
                            img: 'http://f.cloudadmx.com/group2/M00/00/23/wKgABVoGuFaAQ0EWAAAPX5752jw315.png'
                        },
                        {
                            name: '待发货',
                            img: 'http://f.cloudadmx.com/group2/M00/00/23/wKgABloGuFCAWe2wAAAMqVUJj6Y770.png'
                        },
                        {
                            name: '配送中',
                            img: 'http://f.cloudadmx.com/group2/M00/00/23/wKgABVoGuGOAFQCBAAAMqGeUVT8290.png'
                        },
                        {
                            name: '待评价',
                            img: 'http://f.cloudadmx.com/group2/M00/00/23/wKgABloGuVWARqxnAAANo5jhWFE146.png'
                        }
                    ]
                }
            };
        },
        watch: {
            'goods'() {
                this.$nextTick(() => {
                    this._initScroll();
                });
            }
        },
        mounted() {
            this.$nextTick(() => {
                this._initScroll();
            });
        },
        methods: {
            _initScroll() {
                if (!this.scroll) {
                    this.scroll = new BScroll(this.$refs.meBox, {
                        click: true
                    });
                }
            }
        }
    };
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "../../common/stylus/mixin";
.me
    width:100%
    .meHeader
        width:100%
        height:43px
        display:flex
        position:fixed
        top:0
        left:0
        line-height:43px
        z-index:200
        background:#fff
        .left
            flex:1
            text-align:center
        .right
            position:absolute
            top:5px
            left:10px
            & > a
                text-decoration:none
            .icon-set
                font-size:20px
    .line
        width:100%
        border-1px(rgba(7,17,27,0.1))
    .meBox
        position:absolute
        top:43px
        bottom:50px
        left:0
        width:100%
        overflow:hidden
        .meTitle
            width:100%
            height:61px
            display:flex
            margin-top:20px
            margin-bottom:20px
            .left
                flex:0 0 61px
                width:61px
                margin-left:15px
                margin-right:10px
            .center
                flex:1
                .top
                    font-size:15px
                    color:#333333
                    font-weight:700
                    margin-top:15px
                .bottom
                    font-size:11px
                    color:#999999
                    margin-top:5px
            .right
                flex:0 0 10px
                width:10px
                margin-right:15px
                line-height:60px
                color:#999999
        .lineDark
            width:100%
            height:8px
            background:#EEEEEE
        .order
            width:100%
            height:118px
            .orderTitle
                width:100%
                height:44px
                display:flex
                justify-content:space-between
                .orderLeft
                    margin-left:15px
                    line-height:44px
                    font-size:14px
                    color:#333333
                    font-weight:700
                .orderRight
                    margin-right:15px
                    line-height:44px
                    color:#999999
            .orderContent
                width:100%
                & > ul
                    display:flex
                    justify-content:space-around
                    margin-top:15px
                    & > li
                        display:inline-block
                        text-align:center
                        .orderFont
                            font-size:11px
                            margin-top:5px
        .list
            width:100%
            padding-bottom:20px
            & > ul
                & > li
                    display:flex
                    flex-direction:row
                    width:100%
                    height:50px
                    .listLeft
                        flex:0 0 20px
                        width:20px
                        margin-left:15px
                        margin-top:15px
                        margin-right:15px
                    .listRight
                        flex:1
                        margin-right:15px
                        border-bottom:1px solid #EEEEEE
                        display:flex
                        flex-direction:row
                        .innerLeft
                            flex:1
                            line-height:50px
                        .innerRight
                            flex:0 0 10px
                            width:10px
                            line-height:50px
                            color:#999999
</style>


