<template>
<div class="box">
    <div class="boxList">
    <div class="left">
        <router-link to="/me">
            <i class="icon-account"></i>
        </router-link>
    </div>
    <div class="center">
        <input type="text" placeholder="请输入产品关键词或用户名" class="find">
    </div>
    <div class="right">
        <router-link to="/shopCart">
            <i class="icon-cart"></i>
            <el-badge class="mark" :value="this.$store.state.goodsList.length" />
        </router-link>
    </div>
    </div>
    <div class="line border-1px"></div>
</div>

</template>

<script type="text/ecmascript-6">
export default {
     computed: {
         goodsList() {
            return this.$store.state.goodsList.length;
         }
     }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "../../common/stylus/mixin";
.box
    width:100%
    position:fixed
    top:0
    left:0
    z-index:20
    background:#fff
    height:30px
    padding-bottom:10px
    padding-top:10px
    .line
        width:100%
        border-1px(rgba(7,17,27,0.1))
        padding-bottom:10px
    .boxList
        display:flex
    .left
        flex:0 0 20px
        width:20px
        height:20px
        margin: 5px 20px 0 15px
        vertical-align: center
        & > a
            text-decoration: none
            color:#333
    .center
        flex:1
        .find
            width:100%
            height:28px
            border-radius:6px
            background: #EFEFEF url(find.png) no-repeat 20px center;
            background-size:11px 13px
            border:none
            font-size:11px
            text-align:center
    .right
        flex:0 0 20px
        width:20px
        height:20px
        margin:5px 15px 0 20px
        & > a
            text-decoration: none
            color:#333
            .mark
                position:absolute
                top:7px
                right:9px
</style>


