<template>
<div class="favourable" ref="fav">
    <div class="fav-content">
        <ul>
            <li v-for="item in favourable">
                <img :src="item.img" width="100%" height="159"/>
                <div class="darkLine"></div>
            </li>
        </ul>
    </div>
</div>
</template>

<script type="text/ecmascript-6">
import BScroll from 'better-scroll';
export default {
    data() {
        return {
            favourable: [
                {
                    img: 'http://f.cloudadmx.com/group1/M00/00/21/wKgAA1oCtneAVPK5AAEnj4Z2Hh4425.png'
                },
                {
                    img: 'http://f.cloudadmx.com/group1/M00/00/22/wKgABFoCtn-AN5I_AADvMfvg558821.png'
                },
                {
                    img: 'http://f.cloudadmx.com/group1/M00/00/21/wKgAA1oCtoWABpIKAADzgq0V5ZA049.png'
                }
            ]
        };
    },
    watch: {
        'favourable'() {
            this.$nextTick(() => {
                this._initScroll();
            });
        }
    },
    mounted() {
        this.$nextTick(() => {
            this._initScroll();
        });
    },
    methods: {
        _initScroll() {
            if (!this.scroll) {
                this.scroll = new BScroll(this.$refs.fav, {
                    click: true
                });
            }
        }
    }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
.favourable
    width:100%
    position:absolute
    top:50px
    bottom:50px
    left:0
    overflow:hidden
    .darkLine
        width:100%
        height:10px
        background:#EFEFEF
</style>


