<template>
    <div class="home" ref="homeBox">
        <div class="contentBox">
            <div class="home-banner">
                <el-carousel trigger="click" height="158px">
                    <el-carousel-item v-for="item in goods.banner" :key="item.img">
                        <img :src="item.img" width="100%" height="158"/>
                    </el-carousel-item>
                </el-carousel>
            </div>
            <div class="home-content">
                <div class="saleList">
                    <h4>TOP热卖清单 </h4>
                    <div class="sale" ref="wrapper">
                        <ul ref="contentWrapper">
                            <li @click="selectProduct(goodsIn,$event)" v-for="(goodsIn, index) in goods.hotList" class="list">
                                <div class="list-img">
                                    <img :src="goodsIn.img">
                                    <p class="title">{{goodsIn.name}}</p>
                                    <p class="price">￥{{goodsIn.price}}</p>
                                </div>
                            </li>
                        </ul>
                    </div>
                    <div class="all">查看全部 &gt;</div>
                </div>
                <div class="blackLine"></div>
                <div class="classesTitle">
                    <h4>精选专题</h4>
                    <div class="sTitle">
                         <img :src="goods.photos.titleImg" width="100%" height="200"/>
                    </div>
                    <div class="imgBox" ref="imgBox">
                        <ul ref="sImg">
                            <li v-for="(item,index) in goods.photos.list" class="photos">
                                <img :src="item.img" width="100" height="100">
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
        <goods :goodsIn="selectedProduct" ref="goodsIn"></goods>
    </div>
</template>

<script type="text/ecmascript-6">
import BScroll from 'better-scroll';
import goods from '../goods/goods.vue';
export default {
    components: {
        goods
    },
    data() {
        return {
            selectedProduct: {},
            goods: {
                banner: [
                    {
                        img: 'http://f.cloudadmx.com/group2/M00/00/24/wKgABloO61OAQ44AAAGp4CJlMPE351.png'
                    },
                    {
                        img: 'http://f.cloudadmx.com/group1/M00/00/22/wKgABFoCzImAOxP3AAOwYxQHIIs718.png'
                    }
                ],
                hotList: [
                     {
                         id: 1,
                         img: 'http://f.cloudadmx.com/group1/M00/00/20/wKgABFoAYiWAI89rAAB_nqQxtBQ413.png',
                         name: '简约木质笔筒',
                         price: 79,
                         count: 1,
                         add: 34,
                         shopCart: false,
                         recommend: [
                             {
                                 color: '蓝色',
                                 img: 'http://f.cloudadmx.com/group2/M00/00/23/wKgABVoHvUCAbNVZAABseQGEMMU827.png'
                             },
                             {
                                 color: '黑色',
                                 img: 'http://f.cloudadmx.com/group2/M00/00/23/wKgABloHug2AKoBgAABTK7W4yyQ025.png'
                             }
                         ],
                         isActive: true
                     },
                     {
                         id: 2,
                         img: 'http://f.cloudadmx.com/group1/M00/00/20/wKgAA1oAYk-AGjEoAACsSovWRc0379.png',
                         name: '手绘卡通纸胶带',
                         price: 78,
                         add: 34,
                         count: 1,
                         shopCart: false,
                         recommend: [
                             {
                                 color: '白色',
                                 img: 'http://f.cloudadmx.com/group2/M00/00/23/wKgABVoHugWALfQAAABkm9SWO-4383.png'
                             },
                             {
                                 color: '蓝色',
                                 img: 'http://f.cloudadmx.com/group2/M00/00/23/wKgABloHug2AKoBgAABTK7W4yyQ025.png'
                             }
                         ],
                         isActive: true
                     },
                     {
                         id: 3,
                         img: 'http://f.cloudadmx.com/group1/M00/00/20/wKgABFoAYlWAMV3DAABsJ3-QRF8395.png',
                         name: '粉色手账本',
                         price: 78,
                         add: 34,
                         count: 1,
                         shopCart: false,
                         recommend: [
                             {
                                 color: '白色',
                                 img: 'http://f.cloudadmx.com/group2/M00/00/23/wKgABVoHugWALfQAAABkm9SWO-4383.png'
                             },
                             {
                                 color: '蓝色',
                                 img: 'http://f.cloudadmx.com/group2/M00/00/23/wKgABVoHvUCAbNVZAABseQGEMMU827.png'
                             }
                         ],
                         isActive: false
                     },
                     {
                         id: 4,
                         img: 'http://f.cloudadmx.com/group1/M00/00/20/wKgABFoAYiWAI89rAAB_nqQxtBQ413.png',
                         name: '简约木质笔筒',
                         price: 79,
                         add: 34,
                         count: 1,
                         shopCart: false,
                         recommend: [
                             {
                                 color: '蓝色',
                                 img: 'http://f.cloudadmx.com/group2/M00/00/23/wKgABVoHvUCAbNVZAABseQGEMMU827.png'
                             },
                             {
                                 color: '黑色',
                                 img: 'http://f.cloudadmx.com/group2/M00/00/23/wKgABloHug2AKoBgAABTK7W4yyQ025.png'
                             }
                         ],
                         isActive: true
                     }
                ],
                photos: {
                    titleImg: 'http://f.cloudadmx.com/group2/M00/00/20/wKgABloCdX6AYwtpAAV7fUKCYdE366.png',
                    list: [
                        {
                            img: 'http://f.cloudadmx.com/group1/M00/00/20/wKgAA1oAbaCACs5DAAC9VxL5m3E226.png'
                        },
                        {
                            img: 'http://f.cloudadmx.com/group1/M00/00/20/wKgABFoAbZmAbyIqAAFP5LVaXhc946.png'
                        },
                        {
                            img: 'http://f.cloudadmx.com/group1/M00/00/20/wKgAA1oAbaCACs5DAAC9VxL5m3E226.png'
                        },
                        {
                            img: 'http://f.cloudadmx.com/group1/M00/00/20/wKgABFoAbZmAbyIqAAFP5LVaXhc946.png'
                        }
                    ]
                }
            }
        };
    },
    watch: {
        'goods'() {
            this.$nextTick(() => {
                this._initPics();
                this._initScroll();
            });
        }
    },
    mounted() {
        this.$nextTick(() => {
            this._initPics();
            this._initScroll();
        });
    },
    methods: {
        _initScroll() {
            if (!this.scroll) {
                this.scroll = new BScroll(this.$refs.homeBox, {
                    click: true
                });
            }
        },
        _initPics() {
            if (this.goods.hotList) {
                let picWidth = 101;
                let margin = 10;
                let width = (picWidth + margin) * this.goods.hotList.length - margin;
                this.$refs.contentWrapper.style.width = width + 'px';
                this.$nextTick(() => {
                    if (!this.picScroll) {
                        this.picScroll = new BScroll(this.$refs.wrapper, {
                            scrollX: true,
                            eventPassthrough: 'vertical'
                        });
                    } else {
                        this.picScroll.refresh();
                    }
                });
            }
            if (this.goods.photos.list) {
                let picWidth = 101;
                let margin = 10;
                let width = (picWidth + margin) * this.goods.photos.list.length - margin;
                this.$refs.sImg.style.width = width + 'px';
                this.$nextTick(() => {
                    if (!this.pScroll) {
                        this.pScroll = new BScroll(this.$refs.imgBox, {
                            scrollX: true,
                            eventPassthrough: 'vertical'
                        });
                    } else {
                        this.pScroll.refresh();
                    }
                });
            }
        },
        selectProduct(goodsIn, event) {
            this.selectedProduct = goodsIn;
            this.$refs.goodsIn.show();
        }
    }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
        .home
            position:absolute
            top:50px
            bottom:0
            left:0
            width:100%
            overflow:hidden
            .contentBox
                width:100%
                .home-banner
                    width:100%
                .home-content
                    width:100%
                    .blackLine
                        width:100%
                        height:7px
                        background:#EFEFEF
                    .saleList
                        width:100%
                        & > h4
                            text-align:center
                            color:#333333
                            &:after
                                content:' '
                                background:url('./rule.png') no-repeat 0 1px
                                background-size:18px 18px
                                display:inline-block
                                width:18px
                                height:18px
                                margin-left:10px
                            &:before
                                content:' '
                                background:url('./rule.png') no-repeat 0 1px
                                background-size:18px 18px
                                display:inline-block
                                width:18px
                                height:18px
                                margin-right:5px
                        .sale
                            width: 100%
                            overflow: hidden
                            white-space: nowrap
                            & > ul
                                font-size:0
                                padding:0
                                .list
                                    display: inline-block
                                    margin-right: 6px
                                    width: 101px
                                    height: 150px
                                    &:last-child
                                        margin: 0
                                    .list-img
                                        text-align:center
                                        & > img
                                            width:79px
                                            height:77px
                                        .title
                                            font-size:12px
                                            color:#666666
                                            margin-top:20px
                                        .price
                                            font-size:14px
                                            color:#62CED7
                                            margin-top:5px
                                            font-weight:600
                        .all
                            overflow:hidden
                            width:100%
                            text-align:center
                            margin-bottom:20px
                            color:#333333
                            font-size:14px
                    .classesTitle
                        width:100%
                        height:390px
                        & > h4
                            text-align:center
                            color:#333333
                            margin-top:20px
                            margin-bottom:15px
                            &:after
                                content:' '
                                background:url('./rule.png') no-repeat 0 1px
                                background-size:18px 18px
                                display:inline-block
                                width:18px
                                height:18px
                                margin-left:10px
                            &:before
                                content:' '
                                background:url('./rule.png') no-repeat 0 1px
                                background-size:18px 18px
                                display:inline-block
                                width:18px
                                height:18px
                                margin-right:5px
                        .imgBox
                            width:100%
                            overflow:hidden
                            white-space: nowrap
                            margin-top:5px
                            & > ul
                                font-size: 0
                                padding-left:10px
                                & > li
                                    display: inline-block
                                    margin-right: 6px
                                    width: 100px
                                    height: 100px
                                    &:last-child
                                        margin: 0
                                    img
                                        border-radius:8px
</style>


