<template>
<div class="cartcontrol">
    <div class="decrease" @click="countBtn(goodsIn,-1)">-</div>
    <input type="text" class="count" v-model="goodsIn.count"/>
    <div class="add" @click="countBtn(goodsIn,1)">+</div>
</div>
</template>

<script type="text/ecmascript-6">
export default {
    props: {
        goodsIn: {
            type: Object
        }
    },
    methods: {
        countBtn(product, type) {
            console.log('1');
            if (type > 0) {
                product.count++;
            } else {
                product.count--;
                if (product.count < 1) {
                    product.count = 1;
                }
            }
        }
    }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
.cartcontrol
    width:100%
    height:25px
    overflow:hidden
    display:flex
    .decrease,.add
        width:25px
        background:#62CED7
        line-height:25px
        text-align:center
        color:#fff
        border-radius:5px
        cursor:pointer
    .count
        width:40px;
        text-align:center;
        margin-left:10px;
        margin-right:10px;
        color:#666666;
        border:none;
</style>


